.brands {
    transform: translateY(100px);
    margin-bottom: 120px;
    display: flex;
    flex-direction: column;
    background-color: rgb(248, 248, 248);
    height: 100%;

    &__swiper-slider .swiper-wrapper {
        display: flex;
        flex-direction: row;
        gap: 50px;
        width: 500px;
        margin-bottom: 50px;
    }

    &__swiper-slider .swiper-slide {
        background-color: rgb(255, 255, 255);
        display: flex;
        align-items: center;
        justify-content: space-around;
        padding: 0 10px;
        height: 60px;
        box-sizing: border-box;
        width: 210px;
        margin-left: -40px !important;
    }

    &__button {
        background-color: white;
        box-sizing: border-box;
        border: 2px solid rgb(255, 62, 121);
    }

    &__list {
        display: none;

        &-item:nth-child(n + 9):nth-child(-n + 1000) {
            display: none;
            background-color: rgb(255, 255, 255);
            align-items: center;
            justify-content: space-around;
            height: 60px;
            box-sizing: border-box;
            width: 265px;

        }
    }

    &__hide {
        display: none;
        padding-left: 20px;
    }

    &__show {
        display: none;
    }
}